<template>
  <div class="id_card_upload">
          <van-nav-bar
        title="身份认证"
        left-text="返回"
        right-text=""
        left-arrow
        @click-left="goBack"
      />
    <van-cell-group>
      <van-cell title="认证状态">{{getDesc()}}</van-cell>
      <van-cell v-if="errorMsg && status == 3" style="color:red;" title="审核提示">{{errorMsg}}</van-cell>
      <van-field v-if="status ==3 || status==0"
        :value="card_no"
        v-model="card_no"
        :placeholder="'请输入身份证号'"
        label="身份证号"
      />
      <van-cell v-if="status==2 || status==1" title="身份证号">{{card_no}}</van-cell>
    </van-cell-group>
    <div class="title">身份证上传</div>
    <van-row class="upload_hint text-desc">
      <van-icon name="hint"/>
      温馨提示: 请保证图片清晰显示, 否则无法通过审核
    </van-row>
    <van-row class="upload_hint text-desc" style="color: darkred">
      <van-icon name="hint"/>
      温馨提示: 以下图片会添加水印，仅作平台实名认证用，不会泄露信息
    </van-row>
    <div class="title">身份证正面</div>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader capture=“camera” @oversize="maxReadBack" :max-size="5242880" :after-read="onRead1">
            <div class="add_btn">
              <div v-if="front">
                <img :src="front">
                <div class="transparent"></div>
              </div>
              <div v-else>
                <van-icon name="photograph" />
                <p>上传正面</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>
    <van-row v-if="!viewOnly" gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">正面示列</div>
        <img src="../../../assets/images/front.jpg" width="50%">
      </van-col>
    </van-row>

    <div class="title">身份证反面</div>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader capture=“camera” @oversize="maxReadBack" :max-size="5242880" :after-read="onRead2">
            <div class="add_btn">
              <div v-if="reverse">
                <img :src="reverse">
                <div class="transparent"></div>
              </div>
              <div v-else>
                <van-icon name="photograph" />
                <p>上传反面</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>
    <van-row v-if="!viewOnly" gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">反面示列</div>
        <img src="../../../assets/images/back.jpg" width="50%">
      </van-col>
    </van-row>


    <div v-if="status==0 || status==3" class="register-btn">
      <van-button size="large" :loading="isLoading" @click="next" type="danger">
        提交
      </van-button>
    </div>
  </div>
</template>

<script>
import { Row, Col, Toast, Dialog, Button, Uploader ,ImagePreview, Icon } from "vant";
import { ID } from "@/api/user";

export default {
  data() {
    return {
      files: [],
      imgIndex: 0,
      viewOnly: false,
      front: "",
      reverse: "",
      status: -1,
      errorMsg: "",
      card_no: "",
      frontFile:"",
      reverseFile:"",
      isLoading: false
    };
  },

  created() {
    this.initPage();
  },

  methods: {
    initPage() {
      let _this = this;
      _this.$user.getUserInfo(function(data) {
        _this.getSuccess(data);
      });
    },
    getSuccess(data) {
      this.front = data.front;
      this.reverse = data.reverse;
      this.status = data.card_status;
      this.errorMsg = data.card_remark;
      this.card_no = data.card_no;
      if (this.status == 1) {
        this.viewOnly = true;
      }
    },
    getDesc() {
      switch (this.status) {
        case 0:
          return "待提交";
        case 1:
          return "待审核";
        case 2:
          return "已审核通过";
        case 3:
          return "审核不通过";
      }
    },
    onRead1 (files) {
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.front = f1.content;
        that.frontFile = f1.file;
      }
    },
    onRead2 (files) {
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.reverse = f1.content;
        that.reverseFile = f1.file;
      }
    },
    // 压缩图片
    compress(img){
      let originWidth = img.width, // 压缩后的宽
        originHeight=img.height,
        maxWidth = 600, maxHeight = 400,
        quality = 0.8, // 压缩质量
        canvas = document.createElement('canvas'),
        drawer = canvas.getContext("2d");
      canvas.width = maxWidth;
      canvas.height = originHeight/originWidth*maxWidth;
      drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
      let base64 = canvas.toDataURL("image/jpeg", quality); // 压缩后的base64图片
      let file = this.dataURLtoFile(base64,Date.parse(Date())+'.jpg');
      file={content:base64,file:file};
      return file;
    },
    // base64转file
    dataURLtoFile(dataurl,filename){
      let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr],filename,{type:mime});
    },
    // 图片超出限制后 进行压缩
    maxReadBack(files){
      Toast('图片不能超出5M')
    },

    next() {
      if(this.card_no=="" || this.card_no.length < 15 || this.card_no.length > 18){
        Toast.fail("请输入正确的身份证号码");
        return;
      }
      if (!this.frontFile) {
        Toast.fail("请上传身份证正面图片");
        return;
      }
      if (!this.reverseFile) {
        Toast.fail("请上传身份证反面图片");
        return;
      }
      let formdata = new FormData();
      formdata.append("card_no", this.card_no);
      formdata.append("front", this.frontFile);
      formdata.append("reverse", this.reverseFile);
      this.isLoading = true
      this.$reqPost(ID, formdata, {
        hideLoading: true
      }).then(res => {
          this.submitSuccess(res);
        })
        .catch(err => {
           this.isLoading = false
        });
    },
    submitSuccess(res) {
      this.isLoading = false
      let data = res.data;
      if(data.code!=0){
        Toast.fail(data.msg);
        return;
      }
      Dialog.alert({
        title: "资料提交成功",
        message: "请等待平台审核，认证资料审核成功后才能领取任务"
      }).then(() => {
        window.location.reload();
      });
    },
    //返回
    goBack() {
      this.$router.push({path: '/user'});
    }
  },

  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Toast.name]: Toast,
    [Dialog.name]: Dialog,
    [ImagePreview.name]: ImagePreview,
    [Uploader.name]: Uploader,
    [Button.name]: Button
  }
};
</script>

<style lang="scss" scoped>
@import "../../../assets/scss/trans";

.title_status {
  margin-left: 10px;
  color: darkred;
  font-size: 14px;
}
.title {
  margin-left: 10px;
}
.pay_way_title {
  font-weight: bold;
  padding: 0;
}
.id_card_upload {
  margin: 5px 0 5px 0;
  background-color: #fff;
  padding: 15px;
  > div {
    margin-bottom: 15px;
  }
}
.upload_hint {
  position: relative;
  padding-left: 20px;
  line-height: 1.6;
  i {
    position: absolute;
    top: 2px;
    left: 0;
  }
}
.id_card_row {
  > div {
    text-align: center;
  }
  .text-desc {
    margin-bottom: 8px;
  }
  .red_color {
    color: darkred;
    font-size: 14px;
    font-weight: 700;
  }
}
.upload_box .upload_wrap {
  position: relative;
  border: 1px dashed $gray;
  min-height: 145px;
  box-sizing: border-box;
  padding: 5px;
  text-align: center;

  img {
    max-width: 100%;
    height: 120px;
  }

  .van-uploader {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .add_btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $gray;

    p {
      white-space: nowrap;
      margin: 0;
      font-size: 18px;
    }

    i {
      font-size: 26px;
    }
  }
}
.register-btn {
  padding: 0px 10px;
}
</style>



